<template>
<div class="app">

  <div class="navbar navbar-dark bg-dark shadow-sm">
    <div class="container">
      <a href="#" class="navbar-brand d-flex align-items-center">
        <strong>Peter Plantenbak</strong>
      </a>
    </div>
  </div>

  <!-- Full Page Image Header with Vertically Centered Content -->
<div class="hero-image">
  <div class="hero-text">
    <h1 class="font-weight-light">Overview status Peter Plantenbak</h1>
        <p class="font-weight-light">Hier kan je de huidige waardes van sensoren uitlezen</p>
  </div>
</div>
<br>

  <!-- Below you ccn find all the sensor data -->

  <div class="row">
    <div class="col-1"></div>
    <div class="col-5">
        <h2 class="font-weight-light">Bodemvochtigheid</h2>
        
            <div class="LineChart"> 
             <!--We are using the LineChart component imported below in the script and also setting the chart-data prop to the datacollection object-->
              <line-chart :chart-data="datacollection"></line-chart>
            </div>
        
      
    </div>
    <div class="col-5">
        <h2 class="font-weight-light">Temperatuur</h2>
        <div class="temp">
            <p>{{Temperatuur}}C</p>
        </div>
    </div>
    <div class="col-1"></div>
  </div>
  <div class="row">
    <div class="col-1"></div>
    <div class="col-5">
        <h2 class="font-weight-light">Lux index</h2>
        <div class="lux">
            <p>{{lux}} lumen/m</p>
        </div>
        
    </div>
    <div class="col-5">
        <h2 class="font-weight-light">Meldingen</h2>
          <div class="meldingen">
            <p>- vochtigheid is goed</p>
            <p v-if="vlotter">- waterbak is bijna leeg</p> 
        </div>
    </div>
    <div class="col-1"></div>
  </div>
  <div class="row">
    <div class="col-1"></div>
    <div class="col-5">
        <h2 class="font-weight-light">Handmatige besturing</h2>
         <div class="besturing">
            <toggle-button @change="toggled = $event.value"
               :labels="{checked: 'Aan', unchecked: 'Uit'}"
               :color="{checked: '#12e05a', unchecked: '#e01212'}"/> 
            <pre>State: {{toggled}}</pre>
         </div>
    </div>
    <div class="col-5">
        <h2 class="font-weight-light">Waterreservoir</h2>
    </div>
    <div class="col-1"></div>
  </div>

  <div class="timelapse">
      <h2> timelapse</h2>
      <img src="">
      <img :src="urlImage">
  </div>

 
 <!-- This is the footer -->
  <div class="text-muted py-5">
    <div class="container">
        <p class="mb-1">Peter Plantenbak &copy; Circus family</p>
    </div>
 </div>
</div>
</template>
<script src="../../node_modules/osc/dist/osc-browser.min.js"></script>
<script src="https://ajax.googleapis.com/ajax/libs/jquery/3.3.1/jquery.min.js"></script>
<script src="https://cdn.jsdelivr.net/gh/gitbrent/bootstrap4-toggle@3.6.1/js/bootstrap4-toggle.min.js"></script>

            
<script>
import osc from "osc";
import $ from 'jquery';
import LineChart from '../components/Line.vue';


  
            const WS_URL = 'wss://circusfamilyprojects.nl:6084';
            const ws = new WebSocket(WS_URL);
           let urlObject
            



 var port = new osc.WebSocketPort({
          url: "wss://circusfamilyprojects.nl:8084" // ws://localhost:8083 online server wss://circusfamilyprojects.nl:8084
        }); 

 port.open();


export default {
  

  data() {
  return{
  Bodemvochtigheid: '',
  Temperatuur:21,
  lux:0,
  vlotter: true,
  Waterstand: 0, // true is eagle to high
  toggled: false,
  datacollection: null,
  urlImage: "",

    
    
    
     
    
  }},
   created: function(){
             this.OSCMessage();      
          //console.log(message);
         this.fillData ()
         this.getImgUrl()
     },
  components: {
       LineChart,
   


  },
  methods:{
      getImgUrl: function(){
           ws.onopen = () => console.log(`Connected to ${WS_URL}`);
            ws.onmessage = message => {
                 console.log(message.data);
                const arrayBuffer = message.data;
                if(urlObject){
                    URL.revokeObjectURL(urlObject);
                    
                }
                urlObject = URL.createObjectURL(new Blob([arrayBuffer]));
                this.urlImage=  urlObject;
                 //console.log(urlObject )
            }

      },


    greet: function(){
      
      port.send({
        address: "/hello",
          args: ["De slider werkt!"]
         });
    },
      sendSlider: function(){

        port.send({
        address: "/hello",
          args: [this.value]
         });

     },
      OSCMessage: function(){ 
               
        port.on("message", (oscMessage) => {
          
             //console.log(oscMessage);
             this.OSCMessages(oscMessage);
          
      });
     },
      OSCMessages: function(oscMessage){ 

       if(oscMessage.address == '/temp'){
         this.Temperatuur = oscMessage.args[0];
         //console.log(this.Temperatuur);
        }

        if(oscMessage.address == '/bodem'){
         this.Bodemvochtigheid = oscMessage.args[0];
         //console.log(this.Bodemvochtigheid);
        }

          if(oscMessage.address == '/lux'){
         this.lux = oscMessage.args[0];
         //console.log(this.Bodemvochtigheid);
        }

        

        if(oscMessage.address == '/vlotter'){
            console.log(oscMessage.args[0]);
         if(oscMessage.args[0] == 1){
              // waterbak is vol
                this.vlotter = false
         }else{
             // waterbak is leeg
               this.vlotter = true
         }
         
        }

        this.fillData ();
         
     }, 

       fillData () {
        this.datacollection = {
          labels: [ 
            "",
            "",
            "",
            "",
            ],
          datasets: [
            {
              label: '',
             
              fill: true,
               fontColor: "white",
              backgroundColor: 'rgba(43, 208, 180,0.7)',
              borderColor:'rgba(255, 255, 255,0.7)',
              data: [this.Bodemvochtigheid+3,this.Bodemvochtigheid,this.Bodemvochtigheid-2 ,this.Bodemvochtigheid+2],
                pointRadius: 0,
            },
          ], 
        }
      },
   
  },
  
}


</script>

