import Vue from 'vue'
import Router from 'vue-router'


import Home from '@/vieuws/Home'
import Signify from '@/vieuws/Signify'
import Broadcast from '@/vieuws/Broadcast'
import Livestream from '@/vieuws/Livestream'
import Robeco from '@/vieuws/Robeco'
import Gamepad from '@/vieuws/Gamepad'
import Carcontrol from '@/vieuws/Carcontrol'
import Peterplantenbak from '@/vieuws/Peterplantenbak'



Vue.use(Router)

export default new Router({
    routes:[
        {
            path:'/',
            name: '/Home',
            component: Home
        },
        {
            path:'/signify',
            name: '/Sifnify',
            component: Signify
        },
        {
            path:'/broadcast',
            name: '/Broadcast',
            component: Broadcast
        },
        {
           path:'/robeco',
           name: '/Robeco',
           component: Robeco
        },
        {
            path:'/livestream',
            name: '/Livestream',
            component: Livestream
        },
        {
            path:'/gamepad',
            name: '/Gamepad',
            component: Gamepad
        },
        {
            path:'/carcontrol',
            name: '/Carcontrol',
            component: Carcontrol
        },
        {
            path:'/peterplantenbak',
            name: '/Peterplantenbak',
            component: Peterplantenbak
        },
    
        
        

    ]
})